.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  background-color: #1c1c21;
  border-radius: 40px;
  padding-bottom: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
}

.button {
  cursor: pointer;
  border: 0;
  background: #1c1c21;
}
